export default {
  data() {
    return {
      petrol: "",
      amount: "",
    }
  },
  methods: {
    submitOrder() {
      if (this.isBlank(this.amount)) {
        this.$msg('加油金额不能为空且金额需要大于0元');
        return;
      }
      if (!this.isRealNum(this.amount)) {
        this.$msg('加油金额需要为数字且金额需要大于0元');
        return;
      }

      this.$minApi.uniapp_post(this.$base_api.addTransaction, {
        petrolId: this.petrol.petrolId,
        amount: this.amount
      }, "json").then(res => {
        if (res.success) {
          this.$openPage("orderPaySuccess");
        } else {
          this.$msg('下单失败' + res.msg);
        }
      })
    },
    onLoad(option) {
      this.petrol = JSON.parse(decodeURIComponent(option.petrol));
      if (this.petrol == undefined || this.petrol == null) {
        this.$openPage("petrolList");
      }
      uni.showModal({
          title: '提示',
          content: '请于加油站工作人员确认，所选加油站与实际相符',
          success: function (res) {
              if (res.confirm) {
                  console.log('用户点击确定');
              } else if (res.cancel) {
                  console.log('用户点击取消');
              }
          }
      });
    }
  }
}
